@font-face {
  font-family: "Manrope";
  src: url("../fonts/Manrope-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Manrope";
  src: url("../fonts/Manrope-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Manrope";
  src: url("../fonts/Manrope-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Manrope";
  src: url("../fonts/Manrope-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title1 {
  margin: 0;
  font-size: 54px;
  line-height: 120%;
  font-weight: 500;
  color: var(--light-color);
}

.title2 {
  margin: 0;
  font-size: 40px;
  line-height: 120%;
  font-weight: 500;
  color: var(--primary-color);
}

.subtitle1 {
  margin: 0;
  font-size: 32px;
  line-height: 1;
  font-weight: 400;
  line-height: 140%;
  color: var(--light-color);
}

.desc {
  margin: 0;
  font-size: 20px;
  line-height: 140%;
  font-weight: 500;
  color: var(--dark-color);
}
.desc span {
  color: var(--primary-color);
}

.desc2 {
  margin: 0;
  font-size: 18px;
  line-height: 140%;
  font-weight: 500;
  color: var(--dark-color);
}
.desc2 span {
  color: var(--primary-color);
}

.btn {
  display: inline-flex;
  padding: 14px 28px;
  color: var(--light-color);
  font-size: 16px;
  cursor: pointer;
  background: transparent;
  border: 1px solid #ffffff;
  outline: none;
  transition: transform 0.4s ease-in-out;
  position: relative;
  background-color: var(--primary-color);
  border-radius: 8px;
  overflow: hidden;
}
.btn::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-100%);
  background-color: #4886c4;
  transition: transform 0.6s ease-in-out;
}
.btn span {
  display: block;
  position: relative;
  z-index: 4;
}
@media (any-hover: hover) {
  .btn:hover::after {
    transform: translateX(0);
  }
}

.link-btn {
  display: inline-block;
  color: var(--dark-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 140%;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(3px);
}
.header__logo {
  display: block;
  width: 200px;
  height: 80px;
  filter: drop-shadow(-3px 3px 10px rgba(0, 0, 0, 0.3));
}
.header__logo img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.header__buttons {
  display: flex;
  align-items: center;
  gap: 20px;
}
.header__tel {
  display: flex;
  align-items: center;
  gap: 10px;
}
.header__tel span {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  background-color: var(--primary-color);
  border-radius: 8px;
  border: 1px solid #ffffff;
  outline: none;
}
.header__top {
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #252525;
}

.hero {
  padding: 200px 0 150px;
  background-image: linear-gradient(to right, rgba(0, 117, 146, 0.4392156863), transparent), url("./../img/hero.webp");
  background-size: cover;
  background-position: 0% 0, center;
  background-repeat: no-repeat;
  background-size: 80% 100%, cover;
  border-radius: 0 0 30px 30px;
  overflow: hidden;
}
.hero__content {
  max-width: 1200px;
}
.hero__title {
  margin-bottom: 40px;
}
.hero__subtitle {
  margin-bottom: 50px;
  max-width: 80%;
}
.hero__btn {
  font-size: 24px;
  padding: 20px 24px;
}

.nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 40px;
}
.nav ul li a {
  display: inline-block;
  position: relative;
  font-weight: 500;
  font-size: 14px;
  line-height: 171%;
  color: var(--light-color);
  transition: color 0.3s ease-out;
}
.nav ul li a::after {
  content: "";
  position: absolute;
  left: 0;
  top: 100%;
  width: 10%;
  opacity: 0;
  height: 1px;
  transition: opacity 0.3s ease-in, width 0.3s ease-in;
  background-color: currentColor;
}
@media (any-hover: hover) {
  .nav ul li a:hover::after {
    opacity: 1;
    width: 100%;
  }
}

.marquee__items {
  padding: 20px 0;
  display: flex;
  min-width: 100%;
  gap: 40px;
  align-items: center;
}
.marquee__row {
  min-width: 100%;
  flex-shrink: 0;
  display: flex;
  align-content: center;
  gap: 40px;
  animation: marquee 60s linear infinite;
}
.marquee__item {
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  color: var(--dark-color);
  white-space: nowrap;
}

@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
.about {
  padding: 50px 0;
}
.about__content {
  max-width: 60%;
  width: 100%;
}
.about__title {
  margin-bottom: 30px;
}
.about__desc {
  margin-bottom: 50px;
}
.about__pagination {
  position: static;
  transform: none !important;
  width: 100% !important;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  --swiper-pagination-color: var(--primary-color);
}
.about__canvas {
  position: static;
  flex-grow: 1;
  top: 0;
  right: 0;
  aspect-ratio: 1/1;
}
.about__container {
  display: flex;
  align-items: center;
  position: relative;
  gap: 30px;
}
.about__slider {
  padding: 20px;
}
.about__img {
  height: 400px;
  background-color: #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: zoom-in;
  border-radius: 20px;
  border: 1px solid var(--primary-color);
  overflow: hidden;
  box-shadow: 4px 4px 6px rgba(0, 118, 146, 0.2);
}
.about__img img {
  display: block;
  width: 100%;
  height: 100%;
}

.benefits {
  padding-top: 100px;
}
.benefits__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px 20px;
}
.benefits__item {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}
.benefits__img {
  width: 45px;
  height: 45px;
  flex-shrink: 0;
}
.benefits__img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.benefits__title {
  margin: 0;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 700;
  line-height: 140%;
  color: var(--primary-color);
}

.products {
  padding: 100px 0 50px;
}
.products__title {
  margin-bottom: 50px;
}
.products__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
.products__item {
  cursor: pointer;
  min-height: 400px;
  overflow: hidden;
  position: relative;
  background-color: rgba(0, 118, 146, 0.1490196078);
  border-radius: 20px;
  padding: 30px;
  transition: background-color 0.3s ease;
}
@media (any-hover: hover) {
  .products__item:hover {
    background-color: rgba(0, 117, 146, 0.5019607843);
  }
  .products__item:hover .products__img img {
    filter: grayscale(0);
  }
  .products__item:hover .products__capture {
    color: var(--light-color);
  }
}
.products__img {
  position: absolute;
  right: -25%;
  bottom: 0;
  height: 80%;
  width: auto;
}
.products__img img {
  display: block;
  height: 100%;
  width: auto;
  object-fit: contain;
  filter: grayscale(1);
  transition: filter 0.3s ease;
}
.products__capture {
  font-size: 26px;
  line-height: 1.2;
  font-weight: 500;
  color: var(--primary-color);
  transition: color 0.3s ease;
}
.products__content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.footer {
  background-color: #252525;
  color: #fff;
  padding: 60px 0 30px;
}
.footer__top {
  display: grid;
  grid-template-columns: 1.5fr 2fr 2fr;
  gap: 30px;
  margin-bottom: 40px;
}
@media (max-width: 992px) {
  .footer__top {
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }
}
@media (max-width: 576px) {
  .footer__top {
    grid-template-columns: 1fr;
    text-align: center;
  }
}
.footer__logo {
  display: block;
  width: 200px;
}
.footer__logo img {
  width: 100%;
  display: block;
  height: auto;
}
@media (max-width: 576px) {
  .footer__logo {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
}
.footer__title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 20px;
  color: #fff;
}
.footer__nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer__nav-list li {
  margin-bottom: 12px;
}
.footer__nav-list li:last-child {
  margin-bottom: 0;
}
.footer__contacts-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer__contacts-item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  color: #999;
}
@media (max-width: 576px) {
  .footer__contacts-item {
    justify-content: center;
  }
}
.footer__contacts-item:hover {
  color: #4a90e2;
}
.footer__contacts-item:hover .footer__icon {
  fill: #4a90e2;
}
.footer__contacts-item:hover .footer__link {
  color: #4a90e2;
}
.footer__contacts-item:last-child {
  margin-bottom: 0;
}
.footer__icon {
  margin-right: 10px;
  fill: currentColor;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  transition: fill 0.3s ease;
}
.footer__icon svg {
  width: 100%;
  height: 100%;
}
.footer__address {
  font-style: normal;
  color: #999;
  font-size: 14px;
}
.footer__link {
  color: #999;
  text-decoration: none;
  transition: color 0.3s ease;
  font-size: 14px;
}
.footer__link--small {
  font-size: 12px;
}
@media (any-hover: hover) {
  .footer__link:hover {
    color: #4a90e2;
  }
}
.footer__divider {
  height: 1px;
  background-color: rgba(255, 255, 255, 0.1);
  margin: 30px 0;
}
.footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}
@media (max-width: 576px) {
  .footer__bottom {
    flex-direction: column;
    text-align: center;
  }
}
.footer__copyright {
  color: #666;
  font-size: 12px;
}
.footer__links {
  display: flex;
  align-items: center;
  gap: 15px;
}
@media (max-width: 576px) {
  .footer__links {
    flex-direction: column;
    gap: 10px;
  }
}
.footer__separator {
  color: #666;
}
@media (max-width: 576px) {
  .footer__separator {
    display: none;
  }
}

.contacts {
  padding: 50px 0 100px;
}
.contacts__title {
  margin-bottom: 50px;
}
.contacts__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
.contacts__map {
  grid-column: 2 span;
  min-height: 400px;
  border-radius: 30px;
  overflow: hidden;
}
.contacts__info {
  font-style: normal;
}
.contacts__right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
}
.contacts__btn {
  text-align: center;
  justify-content: center;
}
.contacts__row:not(:last-child) {
  margin-bottom: 20px;
}
.contacts__capture {
  margin: 0;
  margin-bottom: 8px;
  font-size: 16px;
  color: var(--dark-color);
}
.contacts__value {
  text-decoration: underline;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  color: var(--primary-color);
}
.contacts__value svg {
  width: 30px;
  height: 30px;
  fill: var(--primary-color);
}